@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap');

/*      UTILITY     */

:root {
  --primary-blue: #006994;
}

body,
html {
    height: 100%;
    font-family: 'Roboto', sans-serif;
}


.flex {
  display: flex;
}

a {
  color: black;
  text-decoration: none; /* no underline */
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

/*      NAVBAR      */
.navbar-container {
  background-color: var(--primary-blue);
  height: 100px;
  align-items: center;
  justify-content: center;
}

.navbar-background {
  background-color: white;
  width: 100%;
  height: 50%;
}

.navbar {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  width: 65%;
  height: 100%;
}

.navlogo {
  align-items: center;
  height: 100%;
}

.navlogo h4 {
  margin-left: 10px;
  color: var(--primary-blue);
  font-weight: bold;
}

.navlogo img {
  height: 95%;
}

.nav a {
  margin-right: 100px;
}

/*      THUMBNAIL     */

.thumbnail-container {
  position: relative;
  width: 100%;
  height: 250px;
  justify-content: center;
  align-items: flex-end;
}

.thumbnail-container::before {
  content: '';
  position: absolute;
  background-image: url('./Images/thumbnail1.jfif');
  background-color: var(--primary-blue);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.75;
  width: 100%;
  height: 100%;
}

.thumbnail-container h2 {
  position: relative;
  text-align: center;
  color: white;
  font-weight: lighter;
}

.thumbnail-text {
  max-width: 600px;
  margin-bottom: 20px;
}

/*      SHOWCASE      */

.showcase {
  min-height: 200px;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  justify-content: space-between;
  margin-top: 50px;
}

.showcase-item {
  text-align: center;
  width: 200px;
  color: white;
  background: var(--primary-blue);
  padding: 30px 25px 5px;
  border-radius: 15px;
  line-height: 1.5rem;
}

.showcase-item a {
  color: white;
  text-decoration: underline;
}

.showcase-item h5 {
  margin-bottom: 25px;
}

/*      RANDOM CATCH      */
.random-catch {
  align-items: center;
  flex-direction: column;
  margin-top: 40px;
}

.catch-item-container {
  flex-wrap: wrap;
}